@import "site_variables";

* {
	margin: 0;
	font-family: 'Roboto';
    font-weight: 400;
}


html {
	// height: 100%;
	min-height: 100%;
	position: relative;
}

.top-strip {
  border-bottom: solid 1px #363636;
  width: 100%;
  height: 40px;
  background: radial-gradient(black 15%, transparent 16%) 0 0, radial-gradient(black 15%, transparent 16%) 8px 8px, radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 0 1px, radial-gradient(rgba(255, 255, 255, 0.1) 15%, transparent 20%) 8px 9px;
  background-color: #131313;
  background-size: 16px 16px;
  background-attachment: fixed;
}

body {
	background-color: $bg-color;
  	margin-bottom: 60px;  /* Margin bottom by footer height */

    color: $light-gray;	
 	font-family: 'Indie Flower', Arial,sans-serif;
	 // background-color: #262626;
	 //background-image: url("/img/classy-fabric.png");
	 //background-image: url("https://www.transparenttextures.com/patterns/classy-fabric.png");
	/* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
}

.footer {
	.footer-wrapper {
		width: 80%;
		// margin: 0 auto;	
		height: 100%;	
		font-size: 0.8em;
		@media (min-width: $med-width ) { font-size: 1.2rem; }
	}
	ul {
		margin: 0;	
		li:first-child {
			margin-left: 0;
		}
		li {
			margin-left: 10px;
			padding-top: 5px;
			padding-bottom: 5px;
			font-size: 2rem;
			color: $icon-color;
			// @media (min-width: $sm-width ) { font-size: 2rem; }
		   }
	}
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;     /* Set the fixed height of the footer here  */
  background: linear-gradient(#2d2d2d, $bg-color);
  border-top: 1px inset #1a1a1a;
  color: white;
  color: $dark-gray;

  svg { 
  	fill: $icon-color; 
  	&:hover { fill: $light-gray; }
  	transform: scale(1.3);
  	vertical-align: middle;
  	
  }

}

.header {
	width: 80%; 
	margin: 0 auto;
	margin-top: 20px;
	margin-bottom: 20px;
	color: $light-gray;
	.fa { color: $icon-color; }
	a { text-decoration: none; color: $light-gray; }

    h1 {
      padding: 0;
      margin-bottom: 20px;
      margin-right: 0;
      font-size: 4rem;
      @media  (min-width: $med-width) { /* medium (48em/768px) and up */
		font-size: 5rem;
	  }
      // font-size: 70px;
      font-weight: 100;
      color: $light-gray;
      font-family: 'Indie Flower', cursive;
    }
    h2 {
      padding: 0;
      padding-bottom: 0;
      margin: 0;
      margin-top: -30px;
      float: left;
      color: $dark-gray;    
      font-size: 20px;
      @media  (min-width: $med-width) { /* medium (48em/768px) and up */
		font-size: 25px;
	  }
    }

    svg { 
    	fill: $icon-color; 
    	transform: scale(0.8) translate(0, -2px);
    	vertical-align: middle;
    }
	.contact a:hover { 
		svg {fill: $light-gray; } 
		span { color: $light-gray; }
	}
}

.content-wrapper {
//	width: 80%;
	width: 100%;
	 @media  (min-width: $sm-width) { width: 80%; }
}

.post-signature {
	color: $dark-gray;
	font-style: italic;

	hr { 
		border: 1.5px solid $dark-gray; 
		border-radius: 1px;
	}
}

@mixin sidebar-panel-factory($color) {
  background-color: $color;
  @media  (min-width: $med-width) {
		&:before &:hover {
			background: radial-gradient(ellipse 150px 100px at right,lighten($color, 10%),lighten($color, 4%), $color); 				
			opacity: 1;
		}
  }
}

@mixin gradient-animation( $color, $transTime) {
	background-size: 100%;
//	background-image: linear-gradient($start, $end);
background-color: $color;
	position: relative;
	z-index: 100;
	&:before {
		//background-image: linear-gradient($end, $start);
			background: radial-gradient(ellipse 150px 100px at right,lighten($color, 10%),lighten($color, 4%), $color); 				
//		background-color: $end;
		content: '';
		display: block;
		height: 100%;
		position: absolute;
		top: 0; left: 0;
		opacity: 0;
		width: 100%;
		z-index: -100;
		transition: opacity $transTime;
	}
	&:hover {
		&:before {
			opacity: 1;
		}
	}
}

