::-webkit-scrollbar {
	height: 10px;
	width: 10px;
	border-radius: 4px;
	background: transparent;
		transition: background-color 0.3s ease;
}
::-webkit-scrollbar:hover {
		transition: background-color 0.3s ease;
	background: lighten(#2b2b2b, 10%);
}
::-webkit-scrollbar-thumb {
	background: #777779;
	border-radius: 4px;
}

}
